import { Box, Typography } from "@mui/material";
import React from "react";
import { useNavigate } from "react-router-dom";
import CategoryBlock from "../UI/block/CategoryBlock";
import CreateBlock from "../UI/block/CreateBlock";
import NewsBlock from "../UI/block/NewsBlock";
import CardItem from "../UI/CardItem";
import AlertSale from "../UI/AlertSale";
import { MayLike } from "../../static/MayLike";
import { OftenBuy } from "../../static/OftenBut";
import { CATALOG_ROUTE } from "../../routes/consts";


const Start = () => {
    // eslint-disable-next-line
    const history = useNavigate();

    return (
        <Box component="main" className="flex-column gap-1" sx={{ mt: 3 }}>
            <NewsBlock />
            <Box className="container no-bg">
                <Typography variant="h5">
                     
                </Typography>
                <Box className="flex-row jcsb gap-1 over-scroll">
                    {Object.keys(MayLike).map((el) => (
                        <Box onClick={() => history(CATALOG_ROUTE)}>
                            <CardItem 
                                img={MayLike[el][0]}
                                title={MayLike[el][2]}
                                price={MayLike[el][1]}
                                id={MayLike[el].indexOf()}
                                key={Math.random()}
                                
                            />
                        </Box>
                    ))}
                </Box>
            </Box>
            <AlertSale />
            <CategoryBlock />
            <CreateBlock />
            <Box className="container no-bg">
                <Typography variant="h5">
                      
                </Typography>
                <Box className="flex-row jcsb gap-1 over-scroll">
                    {Object.keys(OftenBuy).map((el) => (
                        <Box onClick={() => history(CATALOG_ROUTE)}>
                            <CardItem 
                                img={OftenBuy[el][0]}
                                title={OftenBuy[el][2]}
                                price={OftenBuy[el][1]}
                                id={OftenBuy[el].indexOf()}
                                key={Math.random()}
                            />
                        </Box>
                    ))}
                </Box>
            </Box>
        </Box>
    )
}

export default Start;