import React from 'react';
import { ABOUT_ROUTE, BASKET_ROUTE, CATALOG_ROUTE, CREATE_ROUTE, ERROR_ROUTE, PAY_ROUTE, PROD_ROUTE, STARTPAGE_ROUTE} from "./consts";
import ErrorPage from "../components/pages/ErrorPage";
import Start from "../components/pages/Start";
import CreatePage from "../components/pages/CreatePage";
import BasketPage from '../components/pages/BasketPage';
import CatalogPage from '../components/pages/CatalogPage';
import AboutPage from '../components/pages/AboutPage';
import ProdPage from '../components/pages/ProdPage';
import PayAndDeliverPage from '../components/pages/PayAndDeliverPage';

export const publicRoutes = [
    {
        path: STARTPAGE_ROUTE,
        Component: <Start />
    },
    {
        path: ERROR_ROUTE,
        Component: <ErrorPage />
    },
    {
        path: CREATE_ROUTE,
        Component: <CreatePage />
    },
    {
        path: BASKET_ROUTE,
        Component: <BasketPage />
    },
    {
        path: CATALOG_ROUTE,
        Component: <CatalogPage />
    },
    {
        path: ABOUT_ROUTE,
        Component: <AboutPage />
    },
    {
        path: PROD_ROUTE,
        Component: <ProdPage />
    },
    {
        path: PAY_ROUTE,
        Component: <PayAndDeliverPage />
    },
]