import { Box, Typography } from "@mui/material";
import React from "react";
import { useNavigate } from "react-router-dom";
import { PAY_ROUTE, PROD_ROUTE } from "../../../routes/consts";

const NewsBlock = () => {

    const history = useNavigate();

    return(
        <Box className="container no-bg flex-row jcsb gap-1 wrap">
            <Box className="jcfs block-news" onClick={() => history(PAY_ROUTE)}>
                <Typography variant="h4" sx={{ fontSize: '1.8rem' }}>
                     &nbsp;  !
                </Typography>
            </Box>
            <Box className="jcfs block-news"onClick={() => history(PROD_ROUTE)}>
                <Typography variant="h4" sx={{ fontSize: '1.8rem' }}>
                       
                </Typography>
            </Box>
        </Box>
    )
}

export default NewsBlock;