import { Box, Button, Typography } from "@mui/material";
import React, { useContext, useState } from "react";
import AddOutlinedIcon from '@mui/icons-material/AddOutlined';
import RemoveOutlinedIcon from '@mui/icons-material/RemoveOutlined';
import { appContext } from "../../context";

const CardItem = ({img, price, title, id}) => {

    const [number, setNumber] = useState(0);
    const { basket, setBasket } = useContext(appContext);

    const addToBasket = (id) => {
        if (number < 10) {
            let numberel = number;
            setNumber(numberel + 1);
        }
        setBasket([...basket, [img, price, title, id]])
    }
    const removeToBasket = () => {
        if (number > 0) {
            let numberel = number;
            setNumber(numberel - 1);
        }
    }

    console.log(basket)

    return(
        <Box className="CardItem flex-column gap-1">
            <img src={img} alt={title} />
            <Box className="flex-column gap-0">
                <Typography variant="body1" sx={{ fontSize: '1.25rem', fontWeight: '500' }}>
                    {price}&nbsp; 
                </Typography>
                <Typography variant="body2" sx={{ fontSize: '1.0rem' }}>
                    {title}
                </Typography>
                {number === 0
                    ?   <Button 
                            variant="contained"
                            disableElevation
                            onClick={() => addToBasket()}
                        >
                             
                        </Button> 
                    :   <Box className="flex-row">
                            <Button 
                                variant="contained"
                                disableElevation
                                onClick={() => removeToBasket(id)}
                                className="minusButton"
                            >
                                <RemoveOutlinedIcon />
                            </Button> 
                            <Button 
                                variant="contained"
                                disableElevation
                                disableTouchRipple
                                className="numberButton"
                            >
                                {number}
                            </Button> 
                            <Button 
                                variant="contained"
                                disableElevation
                                onClick={() => addToBasket(id)}
                                className="plusButton"
                            >
                                <AddOutlinedIcon />
                            </Button> 
                        </Box>
                }
            </Box>
        </Box>
    )
}

export default CardItem;