import { Button, Typography } from "@mui/material";
import { Box } from "@mui/system";
import React, { useContext } from "react";
import MenuIcon from '@mui/icons-material/Menu';
import LocalMallOutlinedIcon from '@mui/icons-material/LocalMallOutlined';
// import { useContext } from "react";
// import { appContext } from "../../context";
import { useNavigate } from "react-router-dom";
import { ABOUT_ROUTE, BASKET_ROUTE, CATALOG_ROUTE, CREATE_ROUTE, PAY_ROUTE, STARTPAGE_ROUTE } from "../../routes/consts";
import { appContext } from "../../context";

const HeaderOnPage = () => {

    const history = useNavigate();
    const { setBasket } = useContext(appContext);

    // const {theme, setTheme} = useContext(appContext);

    // const switchTheme = () => {
    //     if (theme) {
    //         setTheme(false)
    //         return
    //     }
    //     if (theme === false) {
    //         setTheme(true)
    //         return
    //     }
    // }

    return (
        <Box
            component="header"
            className="flex-column gap-0 wrap pf header"
        >
            <Box className="flex-row jcsb gap-1 wfull wrap topheader">
                <Box 
                    className="flex-row gap-0 title"
                    onClick={() => history(STARTPAGE_ROUTE)}
                >
                    <Typography variant="h3" className="maintitle" sx={{ fontWeight: '500' }}>
                        Urban Suds
                    </Typography>
                    {/* <Typography variant="h3" className="maintitle">
                        Urban
                    </Typography>
                    <Typography variant="h3" className="maintitlehelp">
                        Suds
                    </Typography> */}
                </Box>
                <Box className="flex-row gap-0 wrap header-right">
                    <Button 
                        variant="contained"
                        className="button-default"
                        disableElevation
                        onClick={() => {
                            history(CREATE_ROUTE)
                            setBasket([])
                        }}
                    >
                          
                    </Button>
                    <Button 
                        startIcon={<LocalMallOutlinedIcon fontSize="large" />}
                        variant="contained"
                        className="button-default"
                        disableElevation
                        onClick={() => history(BASKET_ROUTE)}
                    >
                        
                    </Button>
                </Box>
            </Box>
            <Box className="flex-row gap-1 wfull subheader wrap">
                <Box className="subheader-menu">
                    <Button 
                        className="menuIconHeader"
                        startIcon={<MenuIcon />}
                        variant="contained"
                        disableElevation
                        onClick={() => {
                            history(CATALOG_ROUTE)
                            setBasket([])
                        }}
                    >
                        
                    </Button>
                </Box>
                <Box className="flex-row gap-1 wrap subheader-other"> 
                    <Button
                        variant="contained"
                        disableElevation
                        onClick={() => {
                            history(ABOUT_ROUTE)
                            setBasket([])
                        }}
                    >
                         
                    </Button>
                    <Button
                        variant="contained"
                        disableElevation
                        onClick={() => {
                            history(PAY_ROUTE)
                            setBasket([])
                        }}
                    >
                          
                    </Button>
                </Box>
            </Box>
        </Box>
    )
}

export default HeaderOnPage;