import React from "react";
import { Button, Typography } from "@mui/material";
import { STARTPAGE_ROUTE } from "../../routes/consts";
import { useNavigate } from "react-router-dom";
import { Box } from "@mui/system";

const ErrorPage = () => {
    const history = useNavigate();
    
    return (
        <Box component="main" sx={{ mt: 3 }} className="error-page">
            <Box className="flex-column gap-3">
                <Box className="flex-column">
                    <Typography variant="h2">
                         404
                    </Typography>
                    <Typography variant="h2" className="note">
                          
                    </Typography> 
                </Box>
                <Box className="flex-column gap-1">
                    <Button 
                        variant="contained" 
                        onClick={() => history(STARTPAGE_ROUTE)}
                        color="primary"
                        disableElevation
                    >
                          
                    </Button>
                </Box>
            </Box>
        </Box>
    )
}

export default ErrorPage;