import React, { useState } from 'react';
import { BrowserRouter } from 'react-router-dom'
import AppRouter from './components/AppRouter';
import { Container } from '@mui/material';
import { appContext } from "./context/index";
import FooterOnPage from './components/UI/FooterOnPage';
import HeaderOnPage from './components/UI/HeaderOnPage';


const App = () => {
  
  const [theme, setTheme] = useState(false); // true - dark; false - light
  const [basket, setBasket] = useState([]); // true - dark; false - light

  return (
    <appContext.Provider value={{
      theme, setTheme,
      basket, setBasket
    }}>
      <BrowserRouter>
        {/* {theme
          ? <style>:root {"{--font: 'Montserrat', sans-serif;--fontSecond: 'Indie Flower', cursive;--color: #fff;--colorSecond: #6a6a6aa8;--colorOpacity: #ffffffe0;--colorInvert: #000;--colorWhite: #fff;--colorBlack: #000;--colorAction: #1ab13e;--colorActionSecond: #d50100;--helpColor: #c4c3c6; --bgBody: #101010;--bgSecond: #181818;--bgSecondHelper: rgba(0, 0, 0, 0.75);--buttonColorTopComment: #1f3a22;--buttonColorMedComment: #3a341f;--buttonColorBadComment: #3a1f1f;}"}</style>
          : <style>:root {"{--font: 'Montserrat', sans-serif;--fontSecond: 'Indie Flower', cursive;--color: #000;--colorSecond: #c3c3c3;--colorOpacity: #000000e0;--colorInvert: #fff;--colorWhite: #fff;--colorBlack: #000;--colorAction: #1ab13e;--colorActionSecond: #d50100;--helpColor: #a1a1a1; --bgBody: #fff;--bgSecond: #f9f9f9;--bgSecondHelper: rgba(0, 0, 0, 0.06);--buttonColorTopComment: #d3f5d2;--buttonColorMedComment: #f5f0d2;--buttonColorBadComment: #f5d2d2;}"}</style>
        } */}
        <Container maxWidth="xl">
          <HeaderOnPage />
          <AppRouter />
          <FooterOnPage />
        </Container>
      </BrowserRouter>
    </appContext.Provider>
  );
}

export default App;