import { Box, Typography } from "@mui/material";
import React from "react";
import ArrowForwardIosOutlinedIcon from '@mui/icons-material/ArrowForwardIosOutlined';
import TuneOutlinedIcon from '@mui/icons-material/TuneOutlined';
import { CREATE_ROUTE } from "../../../routes/consts";
import { useNavigate } from "react-router-dom";

const CreateBlock = () => {

    const history = useNavigate();

    return(
        <>
            <Box className="container no-bg">
                <Typography variant="h5">
                      ,  ?
                </Typography>
            </Box>
            <Box 
                className="container flex-row gap-1 sale-block" 
                sx={{ mt: -3 }}
                onClick={() => history(CREATE_ROUTE)}
            >
                <Box className="icon-sb" sx={{ width: '72px', height: '72px' }}>
                    <TuneOutlinedIcon sx={{ width: '36px', height: '36px' }} />
                </Box>
                <Box className="flex-row wfull jcsb">
                    <Typography variant="h5">
                            
                    </Typography>
                    <ArrowForwardIosOutlinedIcon />
                </Box>
            </Box>
        </>
    )
}

export default CreateBlock;