import { Box, Typography } from "@mui/material";
import React from "react";

const CardItemBasket = ({img, title, price}) => {

    return(
        <Box className="CardItemBasket flex-row gap-1">
            <img src={img} alt={title} width="5%" />
            <Box className="flex-column gap-0">
                <Typography variant="body1">
                    {title}
                </Typography>
                <Typography variant="body1" sx={{ fontSize: '1.25rem', fontWeight: '500' }}>
                    {price}&nbsp; 
                </Typography>
            </Box>
        </Box>
    )
}

export default CardItemBasket;