import * as React from 'react';
import Button from '@mui/material/Button';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import DialogTitle from '@mui/material/DialogTitle';
import Slide from '@mui/material/Slide';
import { Box, Typography } from '@mui/material';
import ArrowForwardIosOutlinedIcon from '@mui/icons-material/ArrowForwardIosOutlined';

const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="up" ref={ref} {...props} />;
});

export default function AlertSale() {
  const [open, setOpen] = React.useState(false);

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  return (
    <div>
        <Box className="container flex-row gap-1 sale-block" onClick={handleClickOpen}>
            <Box>
                <img src="https://avatars.mds.yandex.net/get-sbs-sd/1680396/1b271226-5e92-4d45-8205-6487ea94b27b/orig" width="72px" height="72px" alt="sale" />
            </Box>
            <Box className="flex-row wfull jcsb">
                <Typography variant="h5">
                     30%   
                </Typography>
                <ArrowForwardIosOutlinedIcon />
            </Box>
        </Box>
      <Dialog
        open={open}
        TransitionComponent={Transition}
        keepMounted
        onClose={handleClose}
        aria-describedby="alert-dialog-slide-description"
        className="alertSALE"
      >
        <img src="https://avatars.mds.yandex.net/get-sbs-sd/1534494/14ec6b59-96c9-4e22-b16b-0c96c2110fe9/orig" width="100%" />
        <DialogTitle>
            <Typography variant="h4">
                  
            </Typography>
        </DialogTitle>
        <DialogContent>
          <DialogContentText id="alert-dialog-slide-description">
            <Typography variant="body1">
                      30%.  .  ().
            </Typography>
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button 
            variant='contained'
            onClick={handleClose} 
            sx={{ width: '100%' }}
            disableElevation
        >
            
        </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}