import { Box, Button, Typography } from "@mui/material";
import React, { useContext } from "react";
import { appContext } from "../../context";
import CardItemBasket from "../UI/CardItemBasket";
import DeleteOutlineOutlinedIcon from '@mui/icons-material/DeleteOutlineOutlined';

const BasketPage = () => {

    const { basket, setBasket } = useContext(appContext);

    const removeBasket = () => {
        setBasket([]);
    }

    return(
        <Box component="main" className="flex-column gap-1" sx={{ mt: 3 }}>
            <Box className="container no-bg">
                <Typography variant="h3">
                    
                </Typography>
                <Box className="flex-row gap-1 wrap">
                    <Box className="flex-column gap-1 basketfinal" sx={{ mt: 3 }}>
                        {basket.length !== 0
                            ?   <>
                                {Object.keys(basket).map((el) => (
                                        <CardItemBasket 
                                            img={basket[el][0]}
                                            title={basket[el][2]}
                                            price={basket[el][1]}
                                            key={Math.random()}
                                        />
                                    ))} 
                                </>
                            :   <Typography variant="h6" className="note">
                                       
                                </Typography>
                        }
                        <Button
                            startIcon={<DeleteOutlineOutlinedIcon />}
                            variant="contained"
                            disableElevation
                            className="basketbuttonRemove"
                            onClick={() => removeBasket()}
                        >
                             
                        </Button>
                    </Box>
                    <Box className="flex-column gap-1 summaryBasket">
                        <Box className="flex-column gap-0">
                            <Typography variant="h4" sx={{ mb: 1 }}>
                                :
                            </Typography>
                            <Typography variant="h6" sx={{ mb: -1, fontWeight: '400' }}>
                                : {basket.length}
                            </Typography>
                            <Typography variant="h5" sx={{ fontSize: '1.25rem', fontWeight: '400' }}>
                                555&nbsp;
                            </Typography>
                        </Box>
                        <Button
                            variant="contained"
                            disableElevation
                            className="basketbutton"
                        >
                               
                        </Button>
                    </Box>
                </Box>
            </Box>
        </Box>
    )
}

export default BasketPage;