import { Box, Typography } from "@mui/material";
import React from "react";
import { useNavigate } from "react-router-dom";
import { CATALOG_ROUTE } from "../../../routes/consts";

const CategoryBlock = () => {

    const history = useNavigate();

    return(
        <Box className="container no-bg">
            <Typography variant="h5">
                 
            </Typography>
            <Box className="flex-row jcsb gap-0 wrap">
                <Box className="jcfs block" onClick={() => history(CATALOG_ROUTE)}>
                    <Typography variant="h4">
                         &nbsp;
                    </Typography>
                </Box>
                <Box className="jcfs block" onClick={() => history(CATALOG_ROUTE)}>
                    <Typography variant="h4">
                        &shy; 
                    </Typography>
                </Box>
                <Box className="jcfs block" onClick={() => history(CATALOG_ROUTE)}>
                    <Typography variant="h4" sx={{ fontSize: '1.7rem' }}>
                        &shy;&shy;&shy; 
                    </Typography>
                </Box>
                <Box className="jcfs block" onClick={() => history(CATALOG_ROUTE)}>
                    <Typography variant="h4" sx={{ fontSize: '1.9rem' }}>
                        &shy;&shy; 
                    </Typography>
                </Box>
                <Box className="jcfs block" onClick={() => history(CATALOG_ROUTE)}>
                    <Typography variant="h4" sx={{ fontSize: '1.9rem' }}>
                         
                    </Typography>
                </Box>
            </Box>
        </Box>
    )
}

export default CategoryBlock;